import { environment, pages } from "projectConfig"
import React from "react"
import { GatsbySeo } from "gatsby-plugin-next-seo"
import Layout from "globalLayoutsIndexPage"
import styled from "styled-components"
import HomePageCover from "projectOrganisms/HomePageCover"
import * as helpers from "globalLibs/helpers"

const PageStyle = styled.div`
  .title-wrapper {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    .experience-info {
      color: ${({ theme }) => theme.tint_orange};
    }
  }
  .my-way-ul {
    overflow: hidden;
    li.active {
      :before {
        background-color: ${({ theme }) => theme.tint_green};
      }
      span {
        color: ${({ theme }) => theme.tint_green};
      }
    }
  }
`

export default function Contacts(props) {
  return (
    <>
      <GatsbySeo
        title={pages.contacts.title}
        description={pages.contacts.description}
        canonical={environment.siteUrl}
        openGraph={{
          url: props.location.href,
          title: pages.contacts.title,
          description: pages.contacts.description,
        }}
      />
      <Layout className="bg-background_quarternary">
        <PageStyle
          as="article"
          className="container-fluid mt-48"
          style={{ maxWidth: "992px" }}
        >
          <h1 className="mb-16">Contacts</h1>
          <hr className="mt-0"></hr>
          <ul>
            <li>
              Email - <a href="mailto:w@res.pm">w@res.pm</a>
            </li>
            <li>
              GitHub -{" "}
              <a href="https://github.com/Attvud" target="blank">
                github.com/Attvud
              </a>
            </li>
            <li>
              Linkedin -{" "}
              <a href="https://www.linkedin.com/in/Attvud" target="blank">
                linkedin.com/in/Attvud
              </a>
            </li>
          </ul>
        </PageStyle>
      </Layout>
    </>
  )
}

